@use "sass:math";

// インナー幅設定
// コンテンツのインナー幅（最も共通の幅が多い部分）
$inner: 1100px;
// ※キュンパス幅ではなくインナーの幅を指す

// 余白
$padding-pc: 25px;
$padding-sp: 20px;

////////////////////
// フォント設定
////////////////////

$base-font-family: 'Noto Sans JP', sans-serif;
$second-font-family: 'Noto Serif JP', serif;
$eb-font-family: 'EB Garamond', serif;;
// ※検証ツールでしっかり反映されているか確認する

body {
  font-family: $second-font-family;
}

// フォントウェイト
$regular: 400;
$normal: 500;
$bold: 700;

////////////////////
// 色の指定
////////////////////

//色指定（共通性のあるものは出来る限り指定すると良い）
$black: #333;
$white: #fff;
$yellow: #796d043b;

// 背景色の指定
body {
  background-color: #e2dfd9;
}
